@import "./SectionComponent";
@import "./GitCommands.css";
@import "./Footer.css";
@import "./ProjectFolderSetup.css";

:root {
  --primary-color: #282c34;
  --secondary-color: #61dafb;
  --light-color: white;
  --dark-color: black;
}

a {
  color: var(--light-color);
}

.App {
  text-align: center;
  min-height: 90vh;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  background-color: #61dafb;
}

html {
  background-color: #61dafb;
  height: 100vh;
}

.imageGroup {
  max-width: 80vw;
  box-shadow: 1px 1px 1px var(--light-color);
  border-radius: 10px;
}

#inputSearchTerm {
  margin: 5px;
  border-radius: 4px;
  padding: 2px;
}

code {
  color: var(--secondary-color);
  text-shadow: rgba(255, 255, 255, 0.3) 1px 1px;
  font-family: Arial, Helvetica, sans-serif;
}

.imageSmallGroup {
  max-width: 10vw;
  box-shadow: 1px 1px 1px var(--light-color);
  border-radius: 10px;
}
